<template>
	<view class="t">
		<!-- 标题&针 -->
		<view class="vinyl-head">
			<view class="logo">
				
			</view>
			<image class="vinyl-neddle" src="../../static/images/needle.png" ></image>
		</view>
		<!-- 中间轮盘 -->
		<view class="vinyl-turntable">
			<image class="vinyl-mask" :src="picUrl" ></image>
			<image class="vinyl-bottom" src="../../static/images/vinyl.png" ></image>
		</view>
		
		<!-- 播放按钮 -->
		<view class="" @tap="palyMusic()">
			<view class="play-btn-play" v-if="play"></view>
			<view class="play-btn-pause" v-else ></view>
		</view>
		<!--  歌词同步滚动 -->
		<lyrics-view :lrc="lrc" :showLrc="showLrc"></lyrics-view>
	</view>
</template>

<script>
	import LyricsView from "../../components/Song/LyricsView.vue";
	export default {
		data() {
			return {
				// 歌曲id
				id: "",
				// 图片地址
				picUrl: "",
				// 播放状态
				play: true,
				// 歌词以及时间线
				lrc: [],
				// 歌词
				showLrc: [],
				// 歌曲地址
				url: "",
				lineNo: 0
			}
		},
		components: {
			LyricsView
		},
		methods: {
			getSong() {
				this.$MyRequest({
					url: "/song/detail",
					data: {
						ids: this.id
					}
				}).then(
					res => {
						let songInfo = res.data.songs[0].al;
						console.log(res.data.songs[0].al);
						this.picUrl = songInfo.picUrl;
					}
				);
				// 获取歌词
				this.$MyRequest({
					url: "/lyric",
					data: {
						id: this.id
					}
				}).then(
					res => {
						// 歌词格式化
						let handleLrc = []
						let lrc = res.data.lrc.lyric.split("\n");
						lrc.forEach(item => {
							if(item) {
								let lrcTime = /\[(.+?)\]/g.exec(item)[1].split(":")
								lrcTime = Number(lrcTime[0])*60+Number(lrcTime[1]);
								// 去掉时间 eg:[01:23.33]
								let word = /(?<=]).*/.exec(item)[0]
								// 空歌词不要
								if(word.length !== 0) {
									handleLrc.push({t: lrcTime, w: word })
									this.showLrc.push(word);
								}
								
								
							}
						})
						this.lrc = handleLrc;
						console.log(handleLrc)
					}
				);
				// 获取url
				this.$MyRequest({
					url: "/song/url",
					data: {
						id: this.id
					}
				}).then(
					res => {
						this.url = res.data.data[0].url;
						this.initMusic();
					}
				);
			},
			// 初始化音乐
			initMusic() {
				this.$innerAudioContext.src = this.url;
				this.$innerAudioContext.play();
				this.$innerAudioContext.onPlay(() => {
				  console.log('开始播放');
				});
				// this.$innerAudioContext.onTimeUpdate(() => {
				// 	console.log(this.$innerAudioContext.currentTime)
				// })
				this.$innerAudioContext.onEnded(() => {
					this.play = false;
					this.lineNo = 0;
					this.$refs.lyrics.$el.scrollTop = 0;
				})
				// this.lyricScroll();
			},
			// 播放/暂停音乐
			palyMusic() {
				if(this.play) {
					// 暂停
					this.$innerAudioContext.pause();
				}else {
					// 播放
					this.$innerAudioContext.play();
				}
				this.play = !this.play;
			},
			// 歌词滚动
			lyricScroll() {
				this.$innerAudioContext.onTimeUpdate(() => {
					let lineNo = this.lineNo
					if(lineNo==this.lrc.length)
						return;
					var curTime = this.$innerAudioContext.currentTime; //播放器时间
					if(this.lrc[lineNo].t<=curTime){
						// this.showLrc = [this.lrc[lineNo-1]?this.lrc[lineNo-1].w:"",this.lrc[lineNo].w,this.lrc[lineNo+1].w]\
						// set 方法才能监听到数组变化
						this.$set(this.showLrc, lineNo, "<div class='focus'>"+this.showLrc[lineNo] +"</div>")
						
						if(lineNo>0) {
							 //console.log(this.$refs.lyrics.$el)
							// 正则去掉样式
							let pre = this.showLrc[lineNo-1].replace(/<\/?.+?\/?>/g,"")
							this.$set(this.showLrc, lineNo-1, pre)
						}
						if(lineNo > 1) {
							this.$nextTick(() => {
								this.$refs.lyrics.$el.scrollTop+=40
								console.log(this.$refs.lyrics.$el.scrollTop)
							})
						}
						
						
						
						this.lineNo++;
						
					}
				})			
			}
			
		},
		onLoad(data) {
			uni.setNavigationBarTitle({
				title: data.name
			})

			this.id = data.id;
			this.getSong()
			
			
		}
	}
</script>

<style lang="scss" scoped>
	@-webkit-keyframes circling {
	    0% {
	        transform: rotate(0deg);
	    }
	
	    to {
	        transform: rotate(360deg);
	    }
	}
	
	@keyframes circling {
	    0% {
	        transform: rotate(0deg);
	    }
	
	    to {
	        transform: rotate(360deg);
	    }
	}
	.t{
		background-color: rgba(0,0,0,.5);
	}
	.vinyl-head {
		position: relative;
		height: 209.739rpx;
		.logo {
			background-image: url(../../static/images/logo.svg);
			background-size: 193.7655rpx 37.503rpx;
			position: absolute;
			top: 15.279rpx;
			left: 20.835rpx;
			width: 193.7655rpx;
			height: 37.503rpx;
		}
		.vinyl-neddle {
			position: absolute;
			left: 46%;
			z-index: 1;
			width: 213.2115rpx;
			height: 345.1665rpx;
			z-index: 1;
		}
	}
	.vinyl-turntable {
		position: relative;
		height: 575.046rpx;
		.vinyl-mask {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -184.0425rpx 0 0 -184.0425rpx;
			width: 368.085rpx;
			height: 368.085rpx;
			
		}
		.vinyl-bottom {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -287.523rpx 0 0 -287.523rpx;
			width: 575.046rpx;
			height: 100%;
	
		}
		.vinyl-bottom, .vinyl-mask {
			animation: circling 20s infinite linear;
		}
	}
	
	
	@mixin play-btn {
		position: absolute;
		background-size: 104.175rpx 104.175rpx;
		top: 445.1745rpx;
		left: 50%;
		z-index: 5;
		margin-left: -52.0875rpx;
		width: 104.175rpx;
		height: 104.175rpx;
	}
	.play-btn-pause {
		@include  play-btn;
		background-image: url("data:image/svg+xml;base64,");
	}
	.play-btn-play {
		@include  play-btn;
		background-image: url("data:image/svg+xml;base64,");
	}
		
</style>
